@import url(https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@200;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
    padding: 0px 30px!important;
    font-size: 18px;
}

.nav-link {
    transition: all 1s;
}

.nav-link:hover {
    color: #274BFC !important;
}
.home_main_div {
    min-height: 79.7vh;
    justify-content: center;
    align-items: center;
}

.home_img {
    box-shadow: 5px 7px 15px rgba(0, 0, 0, 0.9);
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

.home_page_btn {
    padding: 10px 50px;
    font-size: 25px;
    border: 3px solid #274BFC;
    border-radius: 50px;
    color: #274BFC;
    background: transparent;
    transition: all 1s;
}

.home_page_btn:hover {
    background: #274BFC;
    color: #fff;
}
.main_about {
    min-height: 79.7vh;
    justify-content: center;
    align-items: center;
}

.about_img {
    height: 375px !important;
    width: 450px !important;
    box-shadow: 5px 7px 15px rgba(0, 0, 0, 0.9);
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
.skills_div {
    min-height: 79.7vh;
    justify-content: center;
    align-items: center;
}
.contact_div {
    min-height: 79.7vh;
    justify-content: center;
}

.contact_icons {
    padding: 5px;
    box-shadow: 3px -5px 7px #000;
}

.contact_input {
    margin-right: 10px !important;
    width: 47% !important;
}

.contact_msg {
    width: 95% !important;
}

.msg_btn {
    margin-top: 10px!important;
    background: #274BFC!important;
    padding: 10px!important;
    margin-left: 10px!important;
    color: #fff!important;
    transition: all 1s!important;
}

@media (max-width: 360px) {
    .contact_input {
        margin-right: 0 !important;
        width: 100% !important;
    }

    .contact_msg {
        width: 100% !important;
    }
}
.login_div {
    min-height: 79.7vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login_img {
    height: 70%;
    width: 75%;
    margin: auto;
}

.login_input{
    width: 100%;
}

.signin_btn {
    background: #290FBA !important;
    color: #fff !important;
    margin-top: 5px!important;
    border-radius: 5px!important;
}

.forgot_btn {
        margin-left: 10px !important;
        background: #58BAD7 !important;
        color: #fff !important;
        margin-top: 5px!important;
        border-radius: 5px!important;
}

@media (max-width: 360px) {
    .forgot_btn {
        font-size: 14px !important;
        margin-left: 0 !important;
    }
}
.profile_div {
    min-height: 79.7vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.emailVerify_div {
    min-height: 79.7vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.verifyEmail_btn {
    background: #290FBA !important;
    color: #fff !important;
    margin-top: 5px!important;
    border-radius: 5px!important;
}

.resent_otp_btn {
    margin-left: 10px !important;
    background: #58BAD7 !important;
    color: #fff !important;
    margin-top: 5px!important;
    border-radius: 5px!important;
}
footer {
    width: 100%;
    margin-top: auto !important;
    min-height: 35px;
    line-height: 35px;
    text-align: center;
    background-color: #0a183d;
    color: #fff;
    margin-top: auto !important;
}

footer h3 {
    font-size: 25px;
    font-weight: bold;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
}

.my_font {
  font-family: 'Gemunu Libre', sans-serif;
}

@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}
